@import"https://fonts.googleapis.com/css2?family=Manrope:wght@500;800&display=swap";*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%;width:100%;height:100%}body{background-color:#1f2632;color:#cee3e9;position:relative;display:flex;justify-content:center;align-items:flex-start;height:100%;width:100%}@media only screen and (min-width: 50em){body{align-items:center}}.card{background-color:#323a49;border-radius:10px;font-family:"Manrope",sans-serif;font-size:2.2rem;font-weight:800;display:flex;position:relative;flex-direction:column;align-items:center;justify-content:flex-start;margin:10rem auto 0 auto;text-align:center;width:90vw;max-width:35rem;padding:4rem 1rem}@media only screen and (min-width: 50em){.card{font-size:2.8rem;min-width:50rem}}.card__id{color:#52ffa8;font-size:1.2rem;font-weight:500;margin-bottom:1.5rem;margin-top:1.5rem;text-transform:uppercase;letter-spacing:4px}@media only screen and (min-width: 50em){.card__id{font-size:1.4rem}}.card blockquote{padding:2rem;margin-bottom:2rem}.card blockquote::before{content:"“"}.card blockquote::after{content:"”"}.card__divider{width:100%;height:16px;margin-bottom:2rem;background-repeat:no-repeat;background-image:url("../images/pattern-divider-mobile.svg");background-position:center center}@media only screen and (min-width: 50em){.card__divider{background-image:url("../images/pattern-divider-desktop.svg")}}.card__button{margin-top:4rem;height:60px;width:60px;border-radius:50%;border:none;position:absolute;bottom:0;transform:translateY(50%);background:#52ffa8 url(../images/icon-dice.svg) no-repeat;background-position:center center}.card__button:hover,.card__button:active{box-shadow:0 0 10px #52ffa8;cursor:pointer}